<template>
  <div class="container">
    <div class="col-sm-10">
      <h1>Задачи</h1>
      <confirmation :message="confirmationMessage"
                    :alertVariant="confirmationVariant"
                    :showConfirmation="showConfirmation"
                    v-if="showConfirmation">
      </confirmation>
      <button type="button"
              id="task-add"
              class="btn btn-success btn-sm align-left d-block"
              v-b-modal.todo-update-modal
              @click="addTodo()"
      >
        Добавить задачу
      </button>
      <table class="table table-dark table-stripped table-hover">
        <thead class="thead-light">
          <tr>
            <th>Uid</th>
            <th>Описание</th>
            <th>Выполнена?</th>
            <th></th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="(todo, index) in todos" :key="index">
            <td class="todo-uid">{{ todo.uid }}</td>
            <td>{{ todo.description }}</td>
            <td>
              <span v-if="todo.is_completed">Выполнено</span>
              <span v-else>Не выполнено</span>
            </td>
            <td>
              <div class="btn-group" role="group">
              <button type="button"
                      class="btn btn-secondary btn-sm"
                      v-b-modal.todo-update-modal
                      @click="updateTodo(todo)"
              >
                  Обновить
              </button>
                &nbsp;
              <button type="button"
                      class="btn btn-danger btn-sm"
                      @click="deleteTodo(todo)"
              >
                X
              </button>
              </div>
            </td>
          </tr>
        </tbody>

      </table>
      <b-modal ref="updateTodoModal"
              id="todo-update-modal"
              :title="modalTitle"
              hide-footer>
        <b-form @submit="onUpdateSubmit" @reset="onUpdateReset" class="w-100">
        <b-form-group id="form-update-description-group"
                      label="Описание:"
                      label-for="form-update-description-input">
          <b-form-input id="form-update-description-input"
                        type="text"
                        v-model="updateTodoForm.description"
                        required>
          </b-form-input>
        </b-form-group>
        <b-form-group id="form-update-complete-group">
          <b-form-checkbox-group v-model="updateTodoForm.is_completed" id="form-update-checks">
            <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>
        <b-button-group>
          <b-button type="submit" variant="primary">{{modalButton}}</b-button>
          <b-button type="reset" variant="danger">Сброс</b-button>
        </b-button-group>
        </b-form>
      </b-modal>
      <h3>Всего задач: {{ numTasks }}, из них выполнено: {{ numCompleted }}</h3>
    </div>
  </div>
</template>

<style>
button#task-add {
  margin-top: 20px;
  margin-bottom: 20px;
}
h1, td {
  text-align: left;
}
.todo-uid {
  text-align: right;
}
</style>

<script>

import Confirmation from './Confirmation.vue';

export default {
  name: 'Todo',
  data() {
    return {
      todos: [],
      updateTodoForm: {
        uid: 0,
        description: '',
        is_completed: [],
      },
      confirmationMessage: '',
      confirmationVariant: '',
      showConfirmation: false,
      modalTitle: '',
      modalButton: '',
      numTasks: 0,
      numCompleted: 0,
    };
  },
  methods: {
    getTodos() {
      const todoStorage = localStorage.getItem('todoStorage');
      // Проверка наличия todoStorage в localStorage. Если его нет (первый вход) - создаем пустой
      if (todoStorage) {
        this.todos = JSON.parse(localStorage.getItem('todoStorage')).todos;
      } else {
        this.saveTodos();
      }
      // Пересчитываем количество задач - общее/выполненных
      this.numTasks = this.todos.length;
      this.numCompleted = this.todos.filter(element => element.is_completed).length;
    },
    saveTodos() {
      const todoStorage = {
        todos: this.todos,
      };
      // Перед сохранением обновляем uid`ы у всех todo перед сохранением в localStorage
      // Т. о. - в localStorage всегда будет массив todos,
      // где у каждого todo uid равен индексу этого todo в массиве todos
      todoStorage.todos.forEach((element) => {
        const newuid = todoStorage.todos.indexOf(element);
        todoStorage.todos[newuid].uid = newuid;
      });
      localStorage.setItem('todoStorage', JSON.stringify(todoStorage));
    },
    resetForm() {
      this.updateTodoForm.description = '';
      this.updateTodoForm.is_completed = [];
      this.modalTitle = '';
      this.modalButton = '';
      this.getTodos();
    },
    addTodo() {
      const newTodo = {
        uid: undefined,
        description: '',
        is_completed: [],
      };
      this.modalTitle = 'Добавить задачу';
      this.modalButton = 'Добавить';
      this.updateTodoForm = newTodo;
    },
    updateTodo(todo) {
      this.modalTitle = 'Обновить задачу';
      this.modalButton = 'Обновить';
      this.updateTodoForm = todo;
    },
    onUpdateSubmit(event) {
      event.preventDefault();
      if (this.updateTodoForm.uid === undefined) {
        const newTodo = {
          description: '',
          is_completed: [],
        };
        const newuid = this.todos.push(newTodo) - 1;
        this.todos[newuid].uid = newuid;
        this.updateTodoForm.uid = newuid;
        this.confirmationMessage = `Задача "${this.updateTodoForm.description}" добавлена`;
        this.confirmationVariant = 'info';
      } else {
        this.confirmationMessage = 'Задача обновлена';
        this.confirmationVariant = 'dark';
      }
      this.$refs.updateTodoModal.hide();
      this.todos[this.updateTodoForm.uid].description = this.updateTodoForm.description;
      this.todos[this.updateTodoForm.uid].is_completed = this.updateTodoForm.is_completed[0] === 'true';
      this.saveTodos();
      this.showConfirmation = true;
      this.resetForm();
    },
    onUpdateReset(event) {
      event.preventDefault();
      this.$refs.updateTodoModal.hide();
      this.resetForm();
    },
    deleteTodo(todo) {
      this.todos.splice(todo.uid, 1);
      this.saveTodos();
      this.confirmationMessage = 'Задача удалена из списка';
      this.showConfirmation = true;
      this.confirmationVariant = 'warning';
      this.resetForm();
    },
  },
  components: {
    confirmation: Confirmation,
  },
  created() {
    this.getTodos();
  },
};
</script>
